import {bindable, bindingMode} from 'aurelia-framework'

/**
 * Class Paginator (element)
 * Elemento HTML modular para manejar la paginacin
 */
export class Paginator {
  // Decorators
  @bindable({defaultBindingMode: bindingMode.twoWay}) page
  @bindable({defaultBindingMode: bindingMode.twoWay}) totalPages

  /**
   * Inicializa un paginador
   */
  constructor () {
    this.pagination = []
  }

  /**
   * Mtodo que se activa cuando totalPages cambia su valor.
   */
  totalPagesChanged() {
    this.setPagination()
  }

  pageChanged() {
    this.setPagination()
  }
  /**
   * Establece la paginacin de los materiales en la parte inferior.
   */
  setPagination () {
    this.pagination = []
    if (this.page <= 3) {
      while (this.pagination.length < Math.min(this.totalPages, 5)) this.pagination.push(this.pagination.length + 1)
    } else {
      let i = Math.max(this.page - Math.min(2, this.totalPages - this.page), 1)
      while(this.totalPages - i < 4 && i >= 1) i--
      while (this.pagination.length < 5 && i <= this.totalPages) this.pagination.push(i++)
    }
  }


  /**
   * Muestra la primera pgina de materiales en una categora
   */
  goToFirstPage () {
    this.goToPage(1)
  }

  /**
   * Muestra la ltima pgina de materiales en una categora.
   */
  goToLastPage () {
    this.goToPage(this.totalPages)
  }

  /**
   * Muestra la pgina anterior a la actual de materiales en una categora.
   */
  goToPrevPage () {
    if (this.page > 1) {
      this.goToPage(this.page - 1)
    }
  }

  /**
   * Muestra la pgina de materiales siguiente a la actual en una categora.
   */
  goToNextPage () {
    if (this.page < this.totalPages) {
      this.goToPage(this.page + 1)
    }
  }

  /**
   * Muestra una pgina especifica de materiales en una categora.
   * @param {any} page - Pgina a mostrar
   */
  goToPage (page) {
    if (page !== this.page) {
      this.page = page
    }
  }
}
